/* open-sans-300 - latin_cyrillic */
@font-face {
  font-family : 'Open Sans';
  font-style  : normal;
  font-weight : 300;
  src         : url('openSans/open-sans-v34-latin_cyrillic-300.eot'); /* IE9 Compat Modes */
  src         : local(''),
  url('openSans/open-sans-v34-latin_cyrillic-300.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
  url('openSans/open-sans-v34-latin_cyrillic-300.woff2') format('woff2'), /* Super Modern Browsers */
  url('openSans/open-sans-v34-latin_cyrillic-300.woff') format('woff'), /* Modern Browsers */
  url('openSans/open-sans-v34-latin_cyrillic-300.ttf') format('truetype'), /* Safari, Android, iOS */
  url('openSans/open-sans-v34-latin_cyrillic-300.svg#OpenSans') format('svg'); /* Legacy iOS */
}

